@use "styles/stylesFunctions.scss" as stylesFunctions;

.container {
  height: 100vh;
  display: grid;
  place-items: center;
  .Form {
    width: 100%;

    background-color: #f8f8f8;
    padding-top: 100px;
    padding-bottom: 100px;
    padding-left: 100px;
    padding-right: 100px;

    @include stylesFunctions.flexLayout(center, flex-start, column, 20px);
    h3,
    label,
    span,
    p {
      margin: 0;
    }
    h3 {
      width: 100%;
      text-align: center;
      @include stylesFunctions.fontStyles(45px, 34px, 600, #000000, #0000);
    }
    label {
      width: 100%;
      div {
        margin-top: 9px;
        @include stylesFunctions.flexLayout(
          space-between,
          center,
          row,
          initial
        );
        a {
          text-decoration: underline;
          color: #ffaa03;
        }
      }
    }

    input[type="password"],
    input[type="email"] {
      padding: 22px 31px;
      width: 100%;
      border-radius: 5px;
      border: solid 0.5px #d2d2d2;
      @include stylesFunctions.fontStyles(39px, 29px, 300, #000000, #0000);
    }

    input[type="submit"] {
      background-color: #001736;
      border-radius: 5px;
      width: 100%;
      padding-top: 22px;
      padding-bottom: 22px;
      @include stylesFunctions.fontStyles(29px, 22px, 600, #ffffff, #0000);
    }
  }
}
